.InputContainer {
    position: absolute;
    left: 0;
    top: 0;
    width: 55%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3em;
    flex-direction: column;
}
.modeButton {
    margin-bottom: 10%;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

@media only screen and (max-width: 767px) {
    .InputContainer {
        top: unset;
        bottom: 0;
        width: 100%;
        height: 40%;
        font-size: 1em;
    }
}
