.ResultsContainer {
    position: absolute;
    left: 55%;
    top: 0;
    width: 45%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.result {
    bottom: 10%;
    font-size: 3em;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--keys-bg-color);
    min-height: 9%;
}

@media only screen and (max-width: 767px) {
    .ResultsContainer {
        top: 0;
        left: 0;
        width: 100%;
        height: 60%;
        padding-top: 5%;
    }
    .result {
        position: relative;
        font-size: 1em;
        bottom: 0;
        height: 5%;
    }
}
