.Keys {
    background-color: var(--keys-bg-color);
    border: 1px solid var(--keys-border);
    margin: 1.5%;
    padding: 1.5%;
    min-width: 3vw;
    min-height: 8vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5%;
}
@media only screen and (min-width: 768px) {
    .Keys.focused {
        background-color: black;
        color: white;
        border: 1px solid;
    }
}

.Keys.noMatch {
    color: var(--nomatch-key-text-color);
}
.Keys.someMatch {
    color: var(--somematch-key-text-color);
    background-color: var(--somematch-key-color);
}
@media only screen and (max-width: 767px) {
    .Keys {
        min-height: unset;
    }
}
