.TryRow {
    width: 100%;
    min-height: 7%;
    display: flex;
    justify-content: space-evenly;
    font-size: 2.5em;
}

.index {
    margin-inline-end: 2%;
    flex-basis: 10%;
}
.wordContainer {
    display: flex;
    flex-basis: 30%;
    text-align: left;
}
.tryResult {
    flex-basis: 30%;
}

@media only screen and (min-width: 768px) {
    .TryRow:nth-last-child(2) {
        margin-bottom: 5%;
    }
}

@media only screen and (max-width: 767px) {
    .TryRow {
        font-size: 1em;
        min-height: 9%;
    }
}
