.howTo {
    position: absolute;
    z-index: 2;
    background: var(--main-bg-color);
    color: var(--text);
    padding: 3vh 2vw;
    margin: 3vh 2vw;
    text-align: left;
    font-size: 2em;
    width: 90vw;
    height: 86vh;
    border-radius: 2%;
    border: 1vw solid black;
}
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 767px) {
    .howTo {
        padding: 3vh 3vw;
        margin: 3vh 3vw;
        width: 86vw;
        height: 86%;
        font-size: 1em;
    }
}
