.TopComponent {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    margin-bottom: 10%;
}

@media only screen and (max-width: 767px) {
    .TopComponent {
        position: absolute;
        bottom: 0;
        justify-content: space-between;
        margin-bottom: 5%;
    }
}
