.App {
    text-align: center;
    position: absolute;
    width: 100vw;
    height: 100%;
    background-color: var(--main-bg-color);
    color: var(--text);
}
.App.dark {
    --main-bg-color: #2c2c2c;
    --keys-bg-color: #555555;
    --text: #dddddd;
    --nomatch-key-text-color: #888888;
    /* --somematch-key-text-color: lightgreen; */
    --somematch-key-color: #c49000;
    --keys-border: white;
}
.App.light {
    --main-bg-color: #dddddd;
    --keys-bg-color: #bbbbbb;
    --text: #111111;
    --nomatch-key-text-color: #888888;
    /* --somematch-key-text-color: darkgreen; */
    --somematch-key-color: #fdd835;
    --keys-border: black;
}
