.Button {
    font-size: 1em;
    background-color: var(--keys-bg-color);
    color: var(--text);
    border: none;
    border-radius: 5%;
    padding: 2%;
}
@media only screen and (min-width: 768px) {
    .Button.focused {
        background-color: black;
        color: white;
    }
}
@media only screen and (max-width: 767px) {
    .Button {
        margin: 0 4vw;
    }
}
